<template>
	<div class="wrapper">
		<!-- header -->
		<header>
			<p></p>
		</header>
		<!--  -->
		<ul class="form-box">
			<li>
				<div class="title">
					
				</div>
				<div class="content">
					<!--input type="text" v-model="userId" placeholder=""-->
					<input type="text" v-model="userId" placeholder="" :disabled="isLoggingIn"
						pattern="^1[358][0-9]{9}$" maxlength="11">
				</div>
			</li>
			<li>
				<div class="title">
					
				</div>
				<div class="content">
					<!-- <input type="password" v-model="password" placeholder=""> -->
					<input type="password" v-model="password" placeholder="" :disabled="isLoggingIn"
						pattern="[A-Za-z0-9]{6,}" title="6">
				</div>
			</li>
		</ul>
		<div class="button-login">
			<button @click="login"></button>
		</div>
		<div class="button-register">
			<button @click="register"></button>
		</div>
		<!--  -->
		<Footer></Footer>
	</div>
</template>
<script>
	import Footer from '../components/Footer.vue';

	export default {
		name: 'Login',
		data() {
			return {
				userId: '',
				password: '',
				isLoggingIn: false, // 
			}
		},
		methods: {
			login() {
				//var re = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/
				if (!/^1[358][0-9]{9}$/.test(this.userId)) {
					alert('!');
					return;
				}
				if (!/^[A-Za-z0-9]{6,}$/.test(this.password)) {
					alert('6');
					return;
				}
				if (this.userId == '') {
					alert('');
					return;
				}
				if (this.password == '') {
					alert('');
					return;
				}

				this.isLoggingIn = true; // 

				//
				this.$axios.post('UserController/getUserByIdByPass', this.$qs.stringify({
						userId: this.userId,
						password: this.password
					})).then(response => {
						let user = response.data;
						
						if (user =="" ) {
							
							alert('');
						} else {
							//sessionstorageuserImgsession
							//user.userImg = '';
							this.$setSessionStorage('user', user);
							this.$router.go(-1);
						}
					}).catch(error => {
						console.error(error);
					})
					.finally(() => {
						this.isLoggingIn = false; // 
					});
			},
			register() {
				this.$router.push({
					path: 'register'
				});
			}
		},
		components: {
			Footer
		}
	}
</script>
