<template>
	<div class="wrapper">
		<!-- header -->
		<header>
			<p></p>
		</header>
		<!--  -->
		<ul class="form-box">
			<li>
				<div class="title">
					
				</div>
				<div class="content">
					<input type="text" @blur="checkUserId" v-model="user.userId" placeholder="
">
				</div>
			</li>
			<li>
				<div class="title">
					
				</div>
				<div class="content">
					<input type="password" v-model="user.password" placeholder="">
				</div>
			</li>
			<li>
				<div class="title">
					
				</div>
				<div class="content">
					<input type="password" v-model="confirmPassword" placeholder="">
				</div>
			</li>
			<li>
				<div class="title">
					
				</div>
				<div class="content">
					<input type="text" v-model="user.userName" placeholder="">
				</div>
			</li>
			<li>
				<div class="title">
					
				</div>
				<div class="content" style="font-size: 3vw;">
					<input type="radio" v-model="user.userSex" value="1" style="width:6vw;height: 3.2vw;">
					<input type="radio" v-model="user.userSex" value="0" style="width:6vw;height: 3.2vw;">
				</div>
			</li>
		</ul>
		<div class="button-login">
			<button @click="register"></button>
		</div>
		
		<div class="button-tishi">6</div>
		<!--  -->
		<Footer></Footer>
	</div>
</template>
<script>
	import Footer from '../components/Footer.vue';
	export default {
		name: 'Register',
		data() {
			return {
				user: {
					userId: '',
					password: '',
					userName: '',
					userSex: 1
				},
				confirmPassword: ''
			}
		},
		methods: {
			checkUserId() {
				this.$axios.post('UserController/getUserById', this.$qs.stringify({
					userId: this.user.userId,
				})).then(response => {
					if (response.data == 1) {
						this.user.userId = '';
						alert('')
					}
				}).catch(error => {
					console.error(error);
				});
			},
			register() {
				if (this.user.userId == '') {
					alert('');
					return;
				}
				if (this.user.password == '') {
					alert('');
					return;
				}
				if (this.user.password != this.confirmPassword) {
					alert('');
					return;
				}
				if (this.user.userName == '') {
					alert('');
					return;
				}
				//
				this.$axios.post('UserController/saveUser', this.$qs.stringify(
					this.user
				)).then(response => {
					if (response.data > 0) {
						alert('');
						this.$router.go(-1);
					} else {
						alert('');
					}
				}).catch(error => {
					console.error(error);
				});
			}
		},
		components: {
			Footer
		}
	}
</script>
