<template>
	<div class="wrapper">
		<!-- header -->
		<header>
			<div class="fa fa-angle-left" @click="goback()"></div>
			<p></p>
		</header>
		<!--  -->
		<ul class="form-box">
			<li>
				<div class="title">
					
				</div>
				<div class="content">
					{{user.userName}}
				</div>
			</li>
			<li>
				<div class="title">
					
				</div>
				<div class="content">
					<input v-model="user.oldPassword" type="password" placeholder="">
				</div>
			</li>
			<li>
				<div class="title">
					
				</div>
				<div class="content">
					<input v-model="user.new1Password" type="password" placeholder="">
				</div>
			</li>
			<li>
				<div class="title">
					
				</div>
				<div class="content">
					<input v-model="user.new2Password" type="password" placeholder="">
				</div>
			</li>
		</ul>
		<div class="button-login">
			<button @click=" updatePwd()"></button>
		</div>
		<!--  -->
		<Footer></Footer>
	</div>
</template>

<script>
	import Footer from '../components/Footer.vue';

	export default {
		name: 'Register',
		components: {
			Footer
		},
		data() {
			return {
				user: {
					userId: '',
					userName: '',
					password: '',
					oldPassword: '',
					new1Password: '',
					new2Password: '',
				}
			}
		},
		created() {
			this.user = this.$getSessionStorage("user");

		},
		methods: {
			goback() {
				this.$router.go(-1);
			},
			// 
			async updatePwd() {
				let user = this.user;
				// 1. 
				if (user.oldPassword == null ||
					user.new1Password == null ||
					user.new2Password == null) {
					alert("!");
					return;
				} else if (!/^[A-Za-z0-9]{6,}$/.test(user.new1Password)) {
					alert('6');
					return;
				} else if (user.oldPassword != user.password) {
					alert("");
					return;
				}
				// 3. 
				else if (user.oldPassword == user.new1Password) {
					alert("!");
					return;
				}
				// 4. 

				console.log('new1Password:  ' + user.new1Password);
				console.log('new2Password:  ' + user.new2Password);
				if (user.new1Password != user.new2Password) {
					alert("!");
					return;
				}

				// 5. ,
				let url2 = 'UserController/modifyPassword';
				let data = await this.$axios.post(url2, this.$qs.stringify({
					userId: user.userId,
					oldPassword: user.password,
					newPassword: user.new1Password
				}));
				//  
				if (data == 1) {
					this.$router.go(-1);
				}
			}
		}

	}
</script>

<style scoped="scoped">
	/******************  ******************/
	.wrapper {
		width: 100%;
		height: 100%;
	}

	/****************** header ******************/
	.wrapper header {
		width: 100%;
		height: 12vw;
		background-color: #0097FF;
		color: #fff;
		font-size: 4.8vw;

		position: fixed;
		left: 0;
		top: 0;
		z-index: 1000;

		display: flex;
		justify-content: center;
		align-items: center;
	}

	.wrapper header .fa-angle-left {
		margin: 0% 80% 0 0;
		position: absolute;
	}

	/******************  ******************/
	.wrapper .form-box {
		width: 100%;
		margin-top: 12vw;
	}

	.wrapper .form-box li {
		box-sizing: border-box;
		padding: 4vw 3vw 0 3vw;
		display: flex;
		align-items: center;
	}

	.wrapper .form-box li .title {
		flex: 0 0 18vw;
		font-size: 3vw;
		font-weight: 700;
		color: #666;
	}

	.wrapper .form-box li .content {
		flex: 1;
	}

	.wrapper .form-box li .content input {
		border: none;
		outline: none;
		width: 100%;
		height: 4vw;
		font-size: 3vw;
	}

	.wrapper .button-login {
		width: 100%;
		box-sizing: border-box;
		padding: 4vw 3vw 0 3vw;
	}

	.wrapper .button-login button {
		width: 100%;
		height: 10vw;
		font-size: 3.8vw;
		font-weight: 700;
		color: #fff;
		background-color: #38CA73;
		border-radius: 4px;
		border: none;
		outline: none;
	}

	.wrapper .button-register {
		width: 100%;
		box-sizing: border-box;
		padding: 4vw 3vw 0 3vw;
	}

	.wrapper .button-register button {
		width: 100%;
		height: 10vw;
		font-size: 3.8vw;
		font-weight: 700;
		color: #666;
		background-color: #EEE;
		border-radius: 4px;
		border: none;
		outline: none;
		border: solid 1px #DDD;
	}
</style>